html {
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-size: 16px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

*, *:after, *:before {
  box-sizing: inherit;
  overflow-wrap: anywhere;
}

footer {
  margin-top: auto;
}

img {
  width: 100%;
}

.container {
  width: 95%;
  max-width: 118.5rem;
  margin: 0 auto;
  padding: 2rem 0;
}

.centered {
  max-width: 80rem;
}

.hidder {
  display: none !important;
}

.invisible {
  visibility: hidden;
}

a {
  text-decoration: none;
  color: inherit;
  font-size: 1.8rem;
}

textarea {
  resize: vertical;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h1, h2 {
  font-weight: 300;
  text-align: center;
  margin-top: 1rem;
}

h3 {
  font-size: 3rem;
  font-weight: 300;
  text-align: center;
}

p {
  font-size: 2rem;
  line-height: 1.8;
}

.alert {
  background-color: #71b100;
  padding: 0.2rem;
  color: white;
  border-radius: 0.8rem;
  margin: 0 auto 1.5rem auto;
  text-align: center;
}
.alert.error {
  background-color: #af1515;
}

.button {
  cursor: pointer;
  border: none;
  background-color: hotpink;
  padding: 1.5rem 3rem;
  color: white;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  display: inline-block;
  margin: 1.5rem 0;
  width: 100%;
}
@media (min-width: 480px) {
  .button {
    min-width: 20rem;
    width: revert;
  }
}

.button-100w {
  display: block;
  width: 100%;
}

.button-orange {
  background-color: #e08709;
}
.button-orange:hover {
  background-color: #c77808;
}

.button-green {
  background-color: #71b100;
}
.button-green:hover {
  background-color: #619800;
}

.button-red {
  background-color: #af1515;
}
.button-red:hover {
  background-color: #981212;
}

/* layout */
.webp header.index {
  background-image: url(/public/build/img/header.webp);
}

.no-webp header.index {
  background-image: url(/public/build/img/header.jpg);
}

header {
  background-color: #333;
  padding: 2rem 0;
}
header.index {
  background-position: center center;
  background-size: cover;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  header.index {
    height: 74rem;
  }
}
header .bar-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  header .bar-content {
    flex-direction: row;
  }
}
header h1 {
  color: white;
  font-weight: 700;
  line-height: 2;
  margin: 0;
}
@media (min-width: 768px) {
  header h1 {
    max-width: 50%;
  }
}
header .bar-right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  header .bar-right {
    align-items: flex-end;
    gap: 1rem;
  }
}
header .dark-mode-icon, header .mobile-menu {
  width: 3rem;
  cursor: pointer;
}
header .mobile-menu {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  header .mobile-menu {
    display: none !important;
  }
}
header .dark-mode-icon {
  filter: invert(1);
}
header .dark-mode-icon:hover {
  padding: 0.2rem;
}

/* Nav */
.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 2rem;
  }
}

.nav-header {
  visibility: hidden;
  opacity: 0;
  height: 0;
  margin-top: 0;
  transition: all 0s ease-out;
}
@media (min-width: 768px) {
  .nav-header {
    visibility: visible !important;
    opacity: 1 !important;
    height: auto !important;
    margin-top: 0 !important;
  }
}
.nav-header.show-menu {
  visibility: visible;
  opacity: 1;
  height: auto;
  margin-top: 2rem;
  transition: all 0.8s ease-out;
}

.nav-extra-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
.nav-extra-options a {
  color: white;
}
@media (min-width: 768px) {
  .nav-extra-options {
    flex-direction: row;
  }
}

/* Footer */
footer {
  background-color: #333;
  color: white;
  padding-top: 2rem;
}
footer p {
  text-align: center;
}

/* nosotros */
@media (min-width: 768px) {
  .section-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.section-nosotros .icon-nosotros {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .section-nosotros .icon-nosotros {
    margin-bottom: 0;
  }
}
.section-nosotros .icon-nosotros img {
  width: 15rem;
  height: 15rem;
}
.section-nosotros .icon-nosotros p {
  text-align: center;
  margin: 0;
}
.section-nosotros .icon-nosotros h3 {
  text-transform: uppercase;
}

/* propiedades */
/* contácto */
.webp .contacto-bg {
  background-image: url(/public/build/img/encuentra.webp);
}

.no-webp .contacto-bg {
  background-image: url(/public/build/img/encuentra.jpg);
}

.contacto-bg {
  height: 50rem;
  background-position: center center;
  background-size: cover;
  text-align: center;
  color: white;
}
.contacto-bg .container {
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.contacto-bg h2 {
  font-weight: 900;
  font-size: 4rem;
}
.contacto-bg h2, .contacto-bg p {
  margin: 0;
}
.contacto-bg .button {
  margin: 0 auto;
}

/* blog-testimoniales */
@media (min-width: 768px) {
  .section-blog-testimoniales {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}

/* blog */
/* testimoniales */
.section-testimoniales .testimoniales {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.section-testimoniales .testimoniales .testimonial {
  background-color: #71b100;
  padding: 3rem;
  border-radius: 2.5rem;
  color: white;
  display: flex;
  align-items: flex-start;
}
.section-testimoniales .testimoniales .testimonial img {
  width: 6rem;
}
.section-testimoniales .testimoniales .testimonial blockquote {
  font-size: 2.4rem;
  margin: 0;
  line-height: 1.8;
}
.section-testimoniales .testimoniales .testimonial p {
  margin: 0;
  margin-top: 2rem;
  text-align: right;
}

.form fieldset {
  margin-bottom: 1rem;
}
.form p {
  font-size: 1.7rem;
  color: #333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  margin-bottom: 0.2rem;
}
.form label span {
  font-weight: 400;
  text-transform: none;
}
.form input:not([type=submit]), .form select, .form textarea {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: white;
  border: 1px solid #c8c8c8;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 15rem;
  line-height: 1.4;
}
.form .contact-type {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form .contact-type input[type=radio] {
  margin: 0;
  width: auto;
}

.h2-contact {
  margin: 2.5rem 0;
}

#contactType {
  margin-top: 2rem;
}

/* pages */
.blog-posts {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.blog-posts .blog-post {
  display: block;
}
@media (min-width: 480px) {
  .blog-posts .blog-post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
    align-items: center;
  }
  .blog-posts .blog-post:hover {
    border-radius: 0 1rem 1rem 0;
    background-color: rgba(0, 132, 255, 0.096);
    transition: all 0.25s ease-out;
  }
}
.blog-posts .blog-post:hover {
  background-color: rgba(0, 132, 255, 0.096);
}
.blog-posts .blog-post div {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.blog-posts .blog-post h3, .blog-posts .blog-post p {
  margin: 0;
}
.blog-posts .blog-post h3 {
  text-align: left;
  font-weight: 700;
  font-size: 2.3rem;
}
.blog-posts .blog-post h3:after {
  content: "";
  display: block;
  border-bottom: 5px solid #71b100;
  width: 35%;
  margin-top: 1rem;
}
.blog-posts .button {
  margin: 2rem auto;
}

.post-meta span {
  color: #e08709;
  font-weight: 700;
}

@media (min-width: 768px) {
  .nosotros-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}
.nosotros-content blockquote {
  font-weight: 900;
  text-align: left;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
}
@media (min-width: 768px) {
  .nosotros-content blockquote {
    margin: 0;
  }
}

@media (min-width: 850px) {
  .section-propiedades {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }
}
.section-propiedades .card {
  background-color: #eee;
  border: solid 1px #c8c8c8;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 850px) {
  .section-propiedades .card {
    margin-bottom: 0;
  }
}
.section-propiedades .card .card-info {
  padding: 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.section-propiedades .card .card-info .card-bottom {
  margin-top: auto;
}
.section-propiedades .card h3 {
  margin: 0;
}

.button-ver-todas {
  float: right;
}

.card-price {
  font-weight: 700;
  color: #71b100;
  font-size: 3rem;
  margin: 0;
}

.card-icons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
}
.card-icons .card-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.card-icons .card-icon img {
  width: 4rem;
  height: 4rem;
}
.card-icons .card-icon p {
  font-weight: 700;
}

.card-icons-page {
  margin: 0 auto;
}
@media (min-width: 480px) {
  .card-icons-page {
    margin: 0;
    width: 50%;
  }
}

.admin-options {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .admin-options {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }
}
.admin-options img {
  width: 8rem;
  height: 8rem;
}
.admin-options a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #c8c8c8;
  border-radius: 2rem;
  padding: 2rem;
}
.admin-options a:hover {
  background-color: #8e4eff;
  filter: invert(1);
  border: none;
}
.admin-options a:hover h2 {
  font-weight: 400;
}
.admin-options h2 {
  margin: 0;
  margin-top: 1.5rem;
}

.image-update {
  width: 20rem;
  margin-bottom: 2rem;
}

.user-logued {
  max-width: 50rem;
}

.buttons-top-admin {
  display: flex;
  flex-direction: column;
}
@media (min-width: 480px) {
  .buttons-top-admin {
    flex-direction: row;
  }
}
.buttons-top-admin .button:first-of-type {
  margin-right: 1%;
}
@media (min-width: 480px) {
  .buttons-top-admin .button {
    width: 100%;
    white-space: nowrap;
  }
}
@media (min-width: 768px) {
  .buttons-top-admin .button {
    display: inline-block;
    width: revert;
  }
  .buttons-top-admin .button:first-of-type {
    margin-right: 2rem;
  }
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 0.5rem;
}
.alerta.error {
  background-color: #af1515;
}
.alerta.exito {
  background-color: #71b100;
}

table.table-admin {
  margin-top: 2rem;
  width: 100%;
  border-spacing: 0;
  /* responsive */
}
table.table-admin thead {
  background-color: #71b100;
}
table.table-admin thead th {
  color: white;
  padding: 2rem;
  border: 1px solid #c8c8c8;
}
table.table-admin td {
  text-align: center;
  padding: 0;
  border: 1px solid #c8c8c8;
}
table.table-admin p {
  margin: 0.5rem;
}
table.table-admin .image-table {
  width: auto;
  height: 15rem;
  display: block;
  margin: auto auto;
}
table.table-admin th:not(.table-mobile), table.table-admin td:not(.table-mobile) {
  display: none;
}
table.table-admin .table-mobile-head img {
  margin-bottom: 1rem;
}
table.table-admin .table-mobile-buttons .button {
  margin: 0.5rem;
  max-width: 95%;
  padding: 1.4rem;
}
@media (min-width: 480px) {
  table.table-admin .table-mobile-head {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
  table.table-admin .table-mobile-head div {
    border-left: 1px solid #c8c8c8;
  }
  table.table-admin .table-mobile-buttons {
    display: flex;
    justify-content: space-evenly;
  }
}
@media (min-width: 768px) {
  table.table-admin th:not(.table-mobile), table.table-admin td:not(.table-mobile) {
    display: table-cell;
  }
  table.table-admin .table-mobile {
    display: none;
  }
}

/* dark-mode */
.dark-mode {
  color: white;
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode a {
  color: white;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #c8c8c8;
}
.dark-mode .card {
  background-color: #333;
  border: 1px solid #333;
}
.dark-mode .card-bottom .card-price {
  color: #71b100;
}
.dark-mode .card-bottom img {
  filter: invert(100%);
}
.dark-mode .form label, .dark-mode .form legend, .dark-mode .form p {
  color: #c8c8c8;
}
.dark-mode .form input:not([type=submit]), .dark-mode .form select, .dark-mode .form textarea {
  background-color: #333;
  border: 1px solid #4d4d4d;
  color: white;
}
.dark-mode .form input[type=date]::-webkit-calendar-picker-indicator, .dark-mode .form input[type=time]::-webkit-calendar-picker-indicator {
  background-color: #c8c8c8;
  border-radius: 50%;
  padding: 0.4rem;
}
.dark-mode .nosotros-content blockquote {
  color: #c8c8c8;
}
.dark-mode .admin-options a {
  filter: invert(100%);
}
.dark-mode .admin-options a h2 {
  color: black;
}
.dark-mode table.table-admin {
  color: white;
}
.dark-mode table.table-admin th {
  border: 1px solid #333;
}
.dark-mode table.table-admin td {
  border: 1px solid #333;
}

/* demo */
.demo-alert {
  display: block;
  position: fixed;
  bottom: 0rem;
  right: 1rem;
  width: 97%;
  background-color: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(0.5rem);
  color: white;
}
@media (min-width: 480px) {
  .demo-alert {
    bottom: 2rem;
    right: 2rem;
    max-width: 30rem;
    border-radius: 2.2rem;
  }
}
.demo-alert img {
  width: 3.5rem;
  position: absolute;
  right: -1rem;
  top: -1rem;
  background-color: white;
  padding: 0.5rem;
  border-radius: 50%;
  border: 1px solid black;
  cursor: pointer;
}
.demo-alert img:hover {
  filter: invert(1);
}
.demo-alert div {
  padding: 1rem;
}
@media (min-width: 480px) {
  .demo-alert div {
    padding: 2rem;
  }
}
.demo-alert p:first-of-type {
  font-weight: 700;
  letter-spacing: 1px;
}
.demo-alert p, .demo-alert a {
  margin: 0;
  font-size: 1.55rem;
}
.demo-alert a {
  letter-spacing: 2px;
  text-decoration: underline;
}
.demo-alert a:hover {
  background-color: #333;
  padding: 0.4rem;
  border-radius: 3px;
}
.demo-alert span {
  font-weight: 700;
  letter-spacing: 1px;
}